.container {
  display: flex;
  height: 100vh; }
  .container__block {
    box-sizing: border-box; }
  @media (max-width: 768px) {
    .container {
      flex-direction: column; } }

.page__body {
  font-family: "Poppins", "Open Sans", sans-serif;
  background-color: #EEEEEE;
  margin: 0;
  padding: 0;
  color: #292D32; }

.input {
  position: relative; }
  .input input {
    font-size: 12px;
    font-weight: 400;
    color: #B5B7C0;
    box-sizing: border-box;
    width: 216px;
    border: none;
    background-color: #F9FBFF;
    padding: 10px;
    padding-left: 40px;
    border-radius: 5px;
    margin-right: 15px;
    margin-top: 10px; }
    .input input::placeholder {
      color: #B5B7C0; }
    .input input:hover {
      background-color: #e0eaff; }
    .input input:focus {
      outline: 2px solid #3498db;
      color: #292D32; }
  .input::before {
    position: absolute;
    content: " ";
    width: 24px;
    height: 24px;
    background-image: url("../img/svg/search.svg");
    background-size: contain;
    background-repeat: no-repeat;
    top: 50%;
    left: 10px;
    transform: translateY(-70%); }

.pagination {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  list-style: none; }
  .pagination__list {
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none; }
  .pagination__item {
    margin: 0 5px; }
    .pagination__item--bunch {
      margin: 0; }
  .pagination__link {
    width: 25px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: #404B52;
    background-color: #F5F5F5;
    border-radius: 4px;
    transition: background-color 0.3s, color 0.3s;
    font-size: 12px;
    font-weight: 500; }
    .pagination__link:hover {
      background-color: #3498db;
      color: #FFFFFF; }
    .pagination__link--prev, .pagination__link--next {
      font-weight: bold; }
    .pagination__link--bunch {
      pointer-events: none;
      color: #404B52;
      border: none;
      background: none; }
  .pagination__link--active {
    background-color: #5932EA;
    color: #FFFFFF; }

.sidebar {
  width: 306px;
  height: 100%;
  max-width: 306px;
  background-color: #FFFFFF;
  color: #000000;
  padding: 38px 30px 77px;
  display: flex;
  flex-direction: column; }
  .sidebar__title {
    margin-bottom: 38px;
    display: flex;
    align-items: center;
    font-size: 26px;
    font-weight: 600; }
  .sidebar__title-v {
    font-size: 10px;
    color: #9197B3;
    margin-left: 6px;
    margin-top: 8px; }
  .sidebar__logo {
    display: inline-block;
    margin-right: 6px; }
  .sidebar__menu {
    list-style: none;
    padding: 0; }
  .sidebar__item {
    margin-bottom: 20px;
    width: 250px; }
  .sidebar__link {
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 10px;
    color: #9197B3;
    text-decoration: none;
    position: relative;
    font-size: 14px; }
    .sidebar__link::after {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      background-image: url("../img/svg/arrow.svg");
      right: 10px; }
    .sidebar__link:hover {
      color: #FFFFFF;
      background-color: #5932EA; }
      .sidebar__link:hover::after {
        background-image: url("../img/svg/arrow-white.svg"); }
  .sidebar__icon {
    display: inline-block;
    margin-right: 14px; }
  .sidebar__user {
    margin-top: auto; }
  .sidebar__manager, .sidebar__rating, .sidebar__experience {
    margin: 5px 0; }
  @media (max-width: 768px) {
    .sidebar {
      width: 100%;
      max-width: 100%;
      height: auto; } }

.content {
  padding: 46px 78px 46px 20px;
  margin-left: 50px;
  width: 100%; }
  .content__header {
    margin-bottom: 55px; }
  .content__table {
    border: 0.1px solid #FFFFFF; }
  @media (max-width: 1040px) {
    .content {
      padding: 10px; } }
  @media (max-width: 768px) {
    .content {
      padding: 5px; }
      .content__header {
        padding: 10px; } }

.table {
  box-sizing: border-box;
  background-color: #FFFFFF;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 25px;
  margin-bottom: 20px; }
  .table__title {
    margin-bottom: 14px;
    font-size: 22px;
    font-weight: 600; }
  .table__note {
    font-size: 14px;
    font-weight: 400;
    color: #16C098; }
  .table__container {
    width: 100%;
    margin-bottom: 29px;
    padding: 0 15px 0 38px; }
  .table__header {
    padding: 32px 38px 13px;
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .table__top {
    position: relative; }
    .table__top-cell {
      padding: 15px 15px 15px 0;
      text-align: left;
      font-size: 14px;
      font-weight: 500;
      color: #B5B7C0; }
  .table__row {
    padding-left: 50px; }
  .table__cell {
    text-wrap: nowrap;
    padding: 18px 15px 20px 0;
    text-align: left;
    border-bottom: 1px solid #EEEEEE;
    font-size: 14px;
    font-weight: 500; }
  .table__status {
    display: block;
