      color: #DF0404;
      border-color: #DF0404; }
  .table__pagination {
    margin-bottom: 40px;
    margin-right: 33px; }

.table__top-cell:nth-child(1),
.table__cell:nth-child(1) {
  width: 19%;
  margin-left: 20px; }

.table__top-cell:nth-child(2),
.table__cell:nth-child(2) {
