@media (max-width: 1040px) {
  .table {
    padding: 10px; }
    .table__cell {
      padding: 10px; } }

@media (max-width: 768px) {
  .table {
    padding: 5px; }
    .table__title {
      font-size: 1.2em; }
    .table__header__cell {
